export default {
	data() {
		return {
			list: [{
					name: 'fade',
					color: 'red'
				},
				{
					name: 'scale-up',
					color: 'orange'
				},
				{
					name: 'scale-down',
					color: 'olive'
				},
				{
					name: 'slide-top',
					color: 'green'
				}, {
					name: 'slide-bottom',
					color: 'cyan'
				},
				{
					name: 'slide-left',
					color: 'blue'
				},
				{
					name: 'slide-right',
					color: 'purple'
				},
				{
					name: 'shake',
					color: 'mauve'
				}
			],
			animation: '',
			toggleDelay: false
		}
	},
	methods: {
		toggle(e) {
			let that = this
			let animation = e.currentTarget.dataset.class
			that.animation = animation
			setTimeout(function() {
			  that.animation = ''
			}, 1000)
		},
		toggleDelayFn() {
			let that = this
			that.toggleDelay = true
			setTimeout(function() {
			  that.toggleDelay = false
			}, 1000)
		}
	}
}
